import axios from 'axios'
import Vue from '@/main.js'
import store from '@/store'
import {logout} from "@/api/user";

let params = {};
// params.token = store.state.common.token
// params.access_token = store.state.common.access_token

// create an axios instance
const service = axios.create({
    baseURL: store.state.common.apiBaseUrl, // apibase_url
    timeout: 50000, // request timeout
    params,
})

// request interceptor
service.interceptors.request.use(config => {
    // Do something before request is sent
    //config.headers['X-Token'] = ''; // token-- ['X-Token']key 
    // config.headers['Content-Type'] = 'multipart/form-data';

    return config
}, error => {
    // Do something with request error
    console.log('', error); // for debug
    Promise.reject(error);
});

// respone interceptor
service.interceptors.response.use(
    response => {
        if (response.data.success === 1) {
            return response.data;
        } else {
            if (response.data.hasOwnProperty('msg')) {
                switch (response.data.msg) {
                    case 'token':
                        logout();
                        break;
                }
                Vue.$message.warning(response.data.msg);
            }
            throw response.data;
        }
    },
    error => {
        console.log('err' + error)// for debug
        return Promise.reject(error)
    });

export default service;
