import axios from '@/libs/api.request'
import store from '@/store'
import Cookies from 'js-cookie'
import localforage from 'localforage'

const baseUrl = 'users/';

export const getUserList = ({current_page = 0, filter = {}}) => {
    let data = {
        current_page,
        filter
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

