export const login = (d) => {
    if (!store.state.common.token) {
        return getToken()
            .then(res => {
                return axios.request({
                    url: 'login',
                    method: 'post',
                    data: Object.assign(d, {token: store.state.common.token}),
                })
            })
            .then(res => {
                let accessToken = res.data.access_token;
                let userId = res.data.user_id;
                let user = res.data.user;

                store.dispatch('setAccessToken', accessToken)
                store.dispatch('setUserId', userId)
                store.dispatch('setDepartmentId', res.data.user.department_id)
                store.dispatch('setAuths', res.data.auths_format)

                return res;
            })
    } else {
        return axios.request({
            url: 'login',
            method: 'post',
            data: Object.assign(d, {token: store.state.common.token}),
        }).then(res => {
            let accessToken = res.data.access_token;
            let userId = res.data.user_id;
            let user = res.data.user;

            store.dispatch('setAccessToken', accessToken)
            store.dispatch('setUserId', userId)
            store.dispatch('setDepartmentId', res.data.user.department_id)
            store.dispatch('setAuths', res.data.auths_format)

            return res;
        })
    }
}

export const logout = () => {
    Cookies.remove('access_token');
    Cookies.remove('token');
    Cookies.remove('user_id');
    Cookies.remove('department_id');
    localforage.removeItem('auths');

    return store.dispatch('setToken', null)
        .then(() => {
            return store.dispatch('setAccessToken', null)
        });
}

export const getToken = () => {
    return axios.request({
        url: 'token',
        method: 'get',
    })
        .then(res => {
            // store.state.common.token = res.data;
            store.dispatch('setToken', res.data);
        })
}

export const changePassword = (d) => {
    const data = {
        data: d,
    }
    let params = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    let id = store.state.common.user_id;
    return axios.request({
        url: baseUrl + id + '/change_password',
        method: 'post',
        data,
        params,
    })
}
