import Vue from 'vue'
import Vuex from 'vuex'
import Cookies from 'js-cookie'
import localforage from 'localforage'

Vue.use(Vuex)

const common = {
    state: {
        // serverUrl: 'http://party.test/',
        // imgBaseUrl: 'http://party.test/',
        // fileBaseUrl: 'http://party.test/',
        // apiBaseUrl: 'http://party.test/api/',
        // uploadUrl: 'http://party.test/api/upload',
        // websocketBaseUrl: 'ws://127.0.0.1:2345',
        serverUrl: 'http://party_api.lijingbo.top/',
        imgBaseUrl: 'http://party_api.lijingbo.top/',
        fileBaseUrl: 'http://party_api.lijingbo.top/',
        apiBaseUrl: 'http://party_api.lijingbo.top/',
        uploadUrl: 'http://party_api.lijingbo.top/',
        websocketBaseUrl: 'ws://127.0.0.1:2345',
        token: null,
        access_token: null,
        user_id: 0,
        department_id: 0,
    },
    mutations: {
        setToken(state, val) {
            Cookies.set('token', val);
            state.token = val;
        },
        setAccessToken(state, val) {
            Cookies.set('access_token', val);
            state.access_token = val;
        },
        setUserId(state, id) {
            Cookies.set('user_id', id);
            state.user_id = id;
        },
        setDepartmentId(state, id) {
            Cookies.set('department_id', id);
            state.department_id = id;
        },
    },
    actions: {
        setToken({commit}, val) {
            return new Promise((resolve, reject) => {
                commit('setToken', val)
                resolve()
            })
        },
        setAccessToken({commit}, val) {
            return new Promise((resolve, reject) => {
                commit('setAccessToken', val)
                resolve()
            })
        },
        setUserId({commit}, val) {
            return new Promise((resolve, reject) => {
                commit('setUserId', val)
                resolve()
            })
        },
        setDepartmentId({commit}, val) {
            return new Promise((resolve, reject) => {
                commit('setDepartmentId', val)
                resolve()
            })
        },
    },
    getters: {
        getAccessToken: state => {
            if (!state.access_token && Cookies.get('access_token')) {
                state.access_token = Cookies.get('access_token');
                state.user_id = Cookies.get('user_id');
                state.department_id = Cookies.get('department_id');
                state.token = Cookies.get('token');
            }

            if (!state.access_token || state.access_token == 'null' || state.access_token == null) {
                return false;
            }

            return state.access_token;
        }
    }
}

const superAdmin = {
    state: {
        breadcrumb: [{'name': ''}],
    },
    mutations: {},
    actions: {},
    getters: {}
}

const pc = {
    state: {
        auths: [],
    },
    mutations: {
        setAuths(state, val) {
            localforage.setItem('auths', val);
            state.auths = val;
        },
    },
    actions: {
        setAuths({commit}, val) {
            return new Promise((resolve, reject) => {
                commit('setAuths', val)
                resolve()
            })
        },
    },
    getters: {
        getAuths: state => {
            if (!state.auths || !state.auths.length) {
                localforage.getItem('auths', (err, value) => {
                    state.auths = value;
                });
            }
            return state.auths;
        }
    }
}

const chat = {
    state: {
        online_users: [],
        messageReceiveSignal: 0,// watch
    },
}

export default new Vuex.Store({
    modules: {
        common,
        superAdmin,
        pc,
        chat,
    }
})
